.skills-container {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

/* 탭 버튼 */
.tabList {
  position: relative;
  display: flex;
  border-bottom: 1px solid #ccc;
}
.tabBtn {
  flex: 1;
  padding: 0.5rem 1rem;
  text-align: center;
  background: none;
  border: none;
  font-weight: 500;
  cursor: pointer;
}
.tabBtn.active {
  color: #22c55e;
  font-weight: 600;
}

/* 탭 선택 표시 */
.skillTabActiveter {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 2px;
  width: calc(100% / 5);
  background: #22c55e;
  transition: transform 0.3s ease;
  transform: translateX(calc(var(--index, 0) * 100%));
}

.tabContent {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.tabItem {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 2rem;
}

.tabName {
  flex: 1;
  font-weight: 500;
  white-space: nowrap;
}

.tabDesc {
  flex: 2;
  font-size: 0.875rem;
  color: #555;
}

/* md 이상: 세로 탭 */
@media (min-width: 768px) {
  .skills-container {
    flex-direction: row;
  }
  .tabList {
    flex-direction: column;
    border-bottom: none;
    border-right: 1px solid #ccc;
    min-width: 160px;
  }
  .skillTabActiveter {
    bottom: auto;
    left: 0;
    width: 2px;
    height: calc(100% / 5);
    transform: translateY(calc(var(--index, 0) * 100%));
  }
}
